.checkout-page{
    width: 60%;
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin : 50px auto 0;
}
.checkout-page .checkout-header{
    width: 100%;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid darkgray;
}
.checkout-page .checkout-header .header-block{
    text-transform: capitalize;
    width: 23%;
}
.checkout-page .checkout-header .header-block:last-child{
    width: 8%;
}
.checkout-page .total{
    margin-top: 30px;
    margin-left: auto;
    font-size: 36px;
}
.pay{
    margin-left: auto;
    margin-top: 40px;
}

.test-warning{
    color: red;
    display: flex;
    flex-direction: column;
    margin: 30px;
}