.collection-item{
    width: 22vw;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position: relative;
}
.collection-item .image{
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
}
.collection-item .custom-button{
    width: 80%;
    opacity: 0.9;
    position: absolute;
    top: 255px;
    display: none;
}

.collection-item:hover .image{
    opacity: 0.8;
}
.collection-item:hover .custom-button{
    opacity: 0.95;
    display: flex;
}
.collection-item .collection-footer{
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 1rem;
}
.name{
    width: 80%;
    margin-bottom: 15px;
}
.price {
    width: 20%;
}
