.user-image-container{
    width: 100%;
    height: 100%;
    background: #a6e6e6;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    
}

.user-image-container .user-image-icon{
    width: 7em;
    height: 7em;
    border-radius: 50%;
    background: white;
    border: 2px solid black;
}
